.homepage-wrapper {
    color: white;
    display: flex;
    flex-direction: column;
    font-size: 1.5em;
    height: 75vh;
}

.intro-text {
    text-align: left;
    margin: 100px 100px 50px 200px;
}

/*animation which fades in the homepage content in stages, starting 1s after page load, 
with each subsequent section appearing 2s after the last.
Fade in happens over 2 seconds. "animation-fill-mode: backwards" ensures that the content is not visible before the animation begins*/

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

#playwright {
    -webkit-animation: fadein 2s;
            animation: fadein 2s;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

#poet {
       -webkit-animation: fadein 2s;
               animation: fadein 2s;
       -webkit-animation-delay: 3s;
               animation-delay: 3s;
       -webkit-animation-fill-mode: backwards;
               animation-fill-mode: backwards;
}

#spy {
    
      -webkit-animation: fadein 2s;
    
              animation: fadein 2s;
      -webkit-animation-delay: 5s;
              animation-delay: 5s;
      -webkit-animation-fill-mode: backwards;
              animation-fill-mode: backwards;
}

#intro {
      margin-top: 100px;
      -webkit-animation: fadein 2s;
              animation: fadein 2s;
      -webkit-animation-delay: 7s;
              animation-delay: 7s;
      -webkit-animation-fill-mode: backwards;
              animation-fill-mode: backwards;
}
