/*styling for the aspects of the site which apply beyond individual components*/

body {
    text-align: center;
    background-color: black;
    font-family: 'IM Fell Double Pica', serif;
    background-image: url(/LizzieCrockett-module-ii/static/media/lelyportrait.200794a5.jpg);
    background-repeat: no-repeat;
    background-position: -135px -650px;
}

h1 {
     font-family: 'IM Fell Double Pica', serif;
 }

.site-nav {
    display: flex;
    flex-direction: row;
    padding-left: 150px;
}

.site-nav li {
    list-style-type: none;
    display: inline;
    font-family: 'IM Fell Double Pica SC', serif;
    font-size: 2em;
    padding: 0px 50px 0px 50px;
}

.site-nav a {
    color: white;
    text-decoration: none;
}

.site-nav a:hover {
    text-decoration: underline;
}

.site-nav a:visited {
    color: white;
}

.innerpage-wrapper {
    font-family: 'ABeeZee', sans-serif;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    margin: 50px;
}

.page-title {
    margin-bottom: 0px;
    color: white;
}

.content-wrapper {
    display: flex;
    flex-direction: row;
    background-color: white;
    margin: 20px 30px 20px 30px;
}

h1 {
    font-size: 1.5em;
}

/*basic css for screens narrower than 900px*/

@media only screen and (max-width: 900px) {

    body {
        background-image: none;
    }

    .content-wrapper {
        display: flex;
        flex-direction: column;
       }

    .site-nav {
        display: flex;
        flex-direction: column;
        -webkit-padding-start: 0px;
                padding-inline-start: 0px;
    }

    .site-nav li {
        -webkit-padding-start: 0px;
                padding-inline-start: 0px;
        padding: 5px 5px 0px 10px;
    }

    .intro-text {
        text-align: center;
        margin: 50px 100px 50px 100px;
    }
    
    .image-box img {
        width: 100%;
        height: 100%;
    }

    .text-box {
        margin-left: 20px;
    }

    iframe {
        width: 100%;
    }

    .footer {
        display: flex;
        flex-direction: column;
        margin: 1000px 0px 50px 0px;
    }
  
  }
